* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-direction: column;
}

.block {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  color: #fff;
  height: 300px;
  line-height: 3;
  flex-grow: 1;
  text-align: center;
  min-width: 300px;
}

.block--1 {
  background-color: rgb(255, 0, 0);
}

.block--2 {
  background-color: rgb(204, 0, 0);
}

.block--3 {
  background-color: rgb(163, 0, 0);
}

.block--4 {
  background-color: rgb(100, 0, 0);
}

.block--5 {
  background-color: rgb(60, 0, 0);
}

.block--6 {
  background-color: rgb(0, 0, 0);
}

@media (min-width: 600px) {
  .container {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .block--1 {
    order: 0;
  }
  .block--2 {
    order: 1;
  }
  .block--3 {
    order: 3;
  }
  .block--4 {
    order: 2;
  }
  .block--5 {
    order: 4;
  }
  .block--6 {
    order: 5;
  }
}

@media (min-width: 900px) {
  .container {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .block {
    flex-basis: 33.3%;
  }
  .block--1 {
    order: 0;
  }
  .block--2 {
    order: 1;
  }
  .block--3 {
    order: 2;
  }
  .block--4 {
    order: 5;
  }
  .block--5 {
    order: 4;
  }
  .block--6 {
    order: 3;
  }
}
